<template>
  <el-form
    :model="ruleForm2"
    :rules="rules2"
    ref="ruleForm2"
    label-position="left"
    label-width="0px"
    class="demo-ruleForm login-container"
  >
    <h3 class="title">系统注册</h3>
    <el-form-item>
      <el-radio v-model="radio" label="1">手机注册</el-radio>
      <el-radio v-model="radio" label="2" class>邮箱注册</el-radio>
    </el-form-item>

    <el-form-item prop="phone" v-if="radio==1">
      <el-input type="text" v-model="ruleForm2.phone" auto-complete="off" placeholder="手机号"></el-input>
    </el-form-item>
    <el-form-item prop="email" v-if="radio==2">
      <el-input type="text" v-model="ruleForm2.email" auto-complete="off" placeholder="邮箱"></el-input>
    </el-form-item>
    <el-form-item prop="company_name">
      <el-input type="text" v-model="ruleForm2.company_name" auto-complete="off" placeholder="公司名称"></el-input>
    </el-form-item>
    <el-form-item prop="passwd_web">
      <el-input type="password" v-model="ruleForm2.passwd_web" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <!-- <el-checkbox v-model="checked1" checked class="remember">记住密码</el-checkbox> -->
    <!-- <el-checkbox v-model="checked2" checked class="remember right">管理员账号</el-checkbox> -->
    <el-form-item style="width:100%;">
      <el-button
        type="primary"
        style="width:100%;"
        @click.native.prevent="handleSubmit2"
        :loading="registering"
      >注册</el-button>
    </el-form-item>
    <el-link type="primary" @click.native="login()">返回登录</el-link>
  </el-form>
</template>

<script>
import { requestRegister } from '../api/api'
export default {
  data () {
    return {
      radio: '1',
      registering: false,
      ruleForm2: {
        phone: '18158504557',
        email: '961373747@qq.com',
        company_name: 'zjut',
        passwd_web: '123456'
        //         phone: "15700084670",
        // email: "568442079@qq.com",
        // company_name: "浙江工业大学zjut",
        // passwd_web: "123456"
      },
      rules2: {
        phone: [{ required: true, message: '请输入手机号', trigger: 'blur' }],
        email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }],
        company_name: [
          { required: true, message: '请输入公司名称', trigger: 'blur' }
        ],
        passwd_web: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
      // checked1: true,
      // checked2: true
    }
  },
  methods: {
    login () {
      // console.log("in this");
      this.$router.push({ path: '/login' })
    },
    handleReset2 () {
      this.$refs.ruleForm2.resetFields()
    },
    handleSubmit2 (ev) {
      this.$refs.ruleForm2.validate(valid => {
        if (valid) {
          this.registering = true
          var registerParams = {
            // username: this.ruleForm2.account,
            // password: this.ruleForm2.checkPass
            phone: this.ruleForm2.phone,
            email: this.ruleForm2.email,
            company_name: this.ruleForm2.company_name,
            passwd_web: this.ruleForm2.passwd_web
          }
          // console.log(registerParams);
          requestRegister(registerParams).then(data => {
            this.registering = false
            let { msg, code } = data
            if (code !== 200) {
              console.log(msg, code)
              this.$message({
                message: msg,
                type: 'error'
              })
            } else {
              this.$router.push({ path: '/login' })
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.login-container {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;

  .title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .remember {
    margin: 0px 0px 35px 0px;
  }
  .right {
    position: relative;
    float: right;
  }
}
</style>
